    dirs: [],
    pointValueResult: [],
    selectedPoint: {},
    validWizardform: false,
    savingImport: false,
    tab: 0,
    loadingData: false,
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Create Point' : 'Edit Point';
    },
    debugSubscriptions() {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.bacnet;
    },
    setting() {
      return this.$store.state.setting;
    },
    networksData() {
      return this.$store.state.bmNetworks;
    },
  },
  watch: {
    'dialog'(val) {
      val || this.close();
    },
    'dialogDelete'(val) {
      val || this.closeDelete();
    },
    '$route.params.id': function (val, oldVal) {
      if (val != oldVal) {
        this.getData();
      }
    },
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.intervalId);
    next();
  },
  mounted() {
    this.getData();
    //set auto refresh
    if (this.setting.refreshTime && this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(() => {
        this.getData();
        if (this.setting.toastAutoRefresh) {
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    async getData() {
      try {
        this.loadingData = true;
        this.device = await axios.get(`bacnet_master/device/${this.$route.params.id}`);
        this.getDirs();
        this.editedItem.device_uuid = this.device.device_uuid;
      } catch (error) {
      } finally {
        this.loadingData = false;
      }
    },
    getDirs() {
      axios.get(`bacnet_master/network/${this.device.network_uuid}`).then(data => {
        this.dirs = [{ text: 'NETWORKS', link: '/bacnet-master/networks', disabled: false }];
        this.dirs.push({
          text: `NET: ${data.network_name}`,
          link: `/bacnet-master/networks/${data.network_uuid}/devices`,
          disabled: false,
        });
        this.dirs.push({ text: `DEV: ${this.device.device_name}`, disabled: true });
      });
    },
    editItem(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    save() {
      if (this.editedIndex > -1) {
        this.updatePoint(this.editedItem);
      } else {
        this.createPoint(this.editedItem);
      }
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.device.points.indexOf(this.currentItem);
      this.device.points.splice(this.editedIndex, 1);
      await axios.delete(`bacnet_master/point/${this.currentItem.point_uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, { ...POINT_DEFAULT, device_uuid: this.device.device_uuid });
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, { ...POINT_DEFAULT, device_uuid: this.device.device_uuid });
        this.editedIndex = -1;
      });
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport = false;
      this.$nextTick(() => {
        this.dataImport = '';
      });
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form_wizard.validate()) {
          this.savingImport = true;
          this.isClickSaveImport = true;
          try {
            await this.createMutiPoints(this.importArray);
            this.closeDialogImport();
            this.selected = [];
          } catch (ex) {
            console.log(ex);
          } finally {
            this.savingImport = false;
          }
        }
      }
    },
    clickImportJson() {
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : '';
      this.e1 = 1;
    },
    convertExcelToJson(file) {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i => {
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.device_uuid = this.device.device_uuid;
            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(point => {
        delete point.point_uuid;
        return point;
      });
      this.e1 = 2;
    },
    isExistingvalue(item, fieldName) {
      if (!this.device.points) return false;
      const value = item[fieldName];
      const points = _cloneDeep(this.device.points);
      if (this.editedIndex !== -1) {
        points.splice(this.editedIndex, 1); //when edit
      }
      return points.find(p => p[fieldName] == value) != null;
    },
    clickJsonExport() {
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      const data = convertPointsValueToString(JSON.parse(JSON.stringify(array)));
      if (type === 'csv') {
        return exportCSV('points', data);
      }
      if (type === 'excel') {
        return exportExcel('points', data);
      }
      if (type === 'json') {
        return exportJSON('points', JSON.stringify(array));
      }
    },
    updatePoint(data) {
      const _data = updatePostPointData(data);
      axios.patch(`bacnet_master/point/${data.point_uuid}`, _data).then(response => {
        const index = this.device.points.findIndex(n => n.point_uuid == response.point_uuid);
        Object.assign(this.device.points[index], response);
        this.$toasted.show('update success');
        this.close();
      });
    },
    async createPoint(data) {
      const _data = updatePostPointData(data);
      const response = await axios.put(`bacnet_master/point`, _data);
      this.device.points.push(response);
      if (this.importArray && this.importArray.length > 0) {
        const index = this.importArray.findIndex(n => n.point_name == response.point_name);
        if (index > -1) {
          Object.assign(this.importArray[index], response);
        }
      }
      this.$toasted.show('create success');
      this.close();
    },
    saveMassEdit(value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName) {
      if (this.selected.length < 1) {
        return this.$toasted.show('Please select item first!', { type: 'error' });
      }
      this.dialogMassEdit = true;
      this.editAll = { ...POINT_EDIT_ALL_DEFAULT };
      this.editField = fieldName;
    },
    cancelEdit() {},
    openEdit(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    expandItem({ item }) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    onClickRefresh() {
      this.getData();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    async createMutiPoints(points) {
      const promises = [];
      for (const item of points) {
        item.device_uuid = this.device.device_uuid; //update to the current device_uuid
        if (!item.point_uuid) {
          promises.push(this.createPoint(item));
        }
      }
      await Promise.all(promises);
    },

    openPointValueDialog(point) {
      this.selectedPoint = point;
      this.dialogPointValue = true;
      this.pointValueResult = [];
      this.user_options_point_value = {
        read_point_value: { get_priority: false, timeout: 1 },
        write_point_value: { point_write_value: 0, priority: 16, feedback: true, timeout: 1 },
        release_point_priority: { priority: 16, feedback: true, timeout: 1 },
      };
    },

    async savePointValue(data, endPointType) {
      await axios
        .post(`bacnet_master/b/points/${endPointType}/${this.selectedPoint.point_uuid}`, data)
        .then(r => {
          r.point_value_end_point = endPointType;
          this.pointValueResult.push(r);
          this.$toasted.show(endPointType, { type: 'success' });
        })
        .catch(err => {
          this.$toasted.show(endPointType, { type: err });
        });
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>

<style lang="scss">
#no-background-hover::before {
  background-color: transparent !important;
  width: -webkit-fill-available;
  padding: 0;
}
</style>
